@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
*,*:before,*:after{box-sizing:border-box}html,body,div,span,object,iframe,figure,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,code,em,img,small,strike,strong,sub,sup,tt,b,u,i,ol,ul,li,fieldset,form,label,table,caption,tbody,tfoot,thead,tr,th,td,main,canvas,embed,footer,header,nav,section,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline;text-rendering:optimizeLegibility;-webkit-font-smoothing:antialiased;-webkit-text-size-adjust:none;-moz-text-size-adjust:none;-ms-text-size-adjust:none;text-size-adjust:none}footer,header,nav,section,main{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}input{-webkit-appearance:none;border-radius:0}html{scroll-behavior:smooth}body{margin:0 0 50px;font-family:'Roboto', sans-serif}

.card{position:relative;height:250px;width:400px;margin-top:50px}.card--dashed{border-style:dashed;border-radius:4px}.card.highlight{border-color:red}.card__data{position:absolute;top:0;left:0;right:0;bottom:0;display:flex;flex-direction:column;justify-content:flex-end;padding:0 30px 40px}.card__chip{width:50px;height:44px;margin-bottom:20px}.card__number{font-size:32px;font-weight:500;margin:0 0 25px}.card__number__label{margin-right:8px}.card__number__label:last-of-type{margin-right:0}.card__user{display:flex;justify-content:space-between}.card__user__name{font-size:24px;font-weight:500;text-transform:uppercase;margin:0}.card__user__date{font-size:24px;font-weight:500}.card__type{position:absolute;width:100px;height:auto;top:30px;right:30px}

.card-list{width:850px}.card-list__title{font-size:32px;text-align:center}.card-list__cards{display:flex;justify-content:space-between;flex-wrap:wrap}

.app{display:flex;flex-direction:column;align-items:center;margin-top:55px}.app__title{font-size:30px;font-weight:500;margin:0}.card-update{margin:55px auto 0;width:400px}.card-update__btn{color:#fff;font-size:16px;height:38px;width:100%;border:none;border-radius:4px;background:#007bff;margin-bottom:40px}.card-update__btn:hover{background:#0069d9}.card-update__btn:disabled{background:gray}.input-wrapper{display:flex;flex-direction:column;margin-bottom:18px}.input-wrapper:last-of-type{margin-bottom:45px}.input-wrapper__label{font-size:16px;line-height:24px;margin-bottom:15px}.input-wrapper__input{border:1px solid #ced4da;border-radius:4px;height:38px;padding:0 12px}.input-wrapper__error{color:#dc3545;font-size:16px;line-height:20px;font-weight:500}.input-wrapper__multiple{display:flex;justify-content:space-between}.input-wrapper__multiple input{width:80px}

